import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {LoanRecordPage} from "./loan-record/loan-record";

/**
 * Generated class for the MyPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage({name:'my'})
@Component({
  selector: 'page-my',
  templateUrl: 'my.html',
})
export class MyPage {
  info:any={
    name:'万峰岭',
    tel:'155 **** 7384',
    profileURL:'assets/imgs/application-record/profile.png'
  };
  items=[];
  constructor(public navCtrl: NavController, public navParams: NavParams) {
    this.items = [
      {
        'title': '客户贷款记录查询',
        'icon': 'icon-ic_dkjlcx',
        'key':'loan-record',
      },
      {
        'title': '我的客户',
        'icon': 'icon-ic_customer',
        'key':'my-customer',
      },
      {
        'title': '我的消息',
        'icon': 'icon-ic_message',
        'key':'my-message',
      },
      {
        'title': '客户预约',
        'icon': 'icon-ic_order',
        'key':'appointment-customer',
      },
    ]
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad MyPage');
  }

  openNavDetailsPage(item) {
    switch (item){
      case 'loan-record':
        this.navCtrl.push('loan-record');
        break;
      case 'my-customer':this.navCtrl.push('my-customer');break;
      case 'my-message':this.navCtrl.push('my-message');break;
      case 'appointment-customer':this.navCtrl.push('appointment-customer');break;
      case 'my-detail':this.navCtrl.push('my-detail');break;
      default:console.log('-');
    }
  }

}



